.product-information {
  padding-top: 74px;
  display: flex;
  flex-flow: column;
  align-items: flex-start;
}

.product-information h1 {
  @include page-title();

  margin-bottom: 21px;
  margin-left: 3px;
}

.product-information__features {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-flow: wrap;
  align-items: flex-start;
  margin-left: (10px - 12px);
  margin-bottom: 22px;
}

.product-information__features-item {
  display: flex;
  flex-flow: column;
  align-items: center;
  margin-left: 12px;

  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;

  opacity: 0.5;

  &--engine-type-gasoline {
    &::before {
      content: "";
      display: flex;
      width: 44px;
      height: 40px;

      background: transparent url("../../images/engine-type-gasoline-icon.svg") no-repeat center center / 24px 22px;
    }
  }

  &--transmission-manual {
    &::before {
      content: "";
      display: flex;
      width: 40px;
      height: 40px;

      background: transparent url("../../images/transmission-manual-icon.svg") no-repeat center center / 40px 40px;
    }
  }

  &--horsepower {
    &::before {
      content: "";
      display: flex;
      width: 62px;
      height: 40px;

      background: transparent url("../../images/horsepower-icon.svg") no-repeat center center / 32px 21px;
    }
  }

  &--engine-capacity {
    &::before {
      content: "";
      display: flex;
      width: 68px;
      height: 40px;

      background: transparent url("../../images/engine-capacity-icon.svg") no-repeat center center / 30px 23px;
    }
  }
}

.product-information__prices {
  display: flex;
  flex-flow: wrap;
  align-items: center;
  margin-bottom: 37px;
  margin-left: 3px;
}

.product-information__prices b {
  display: flex;
  margin-right: 35px;

  font-style: normal;
  font-weight: 700;
  font-size: 35px;
  line-height: 48px;
  white-space: pre;
}

.product-information__prices del {
  position: relative;
  display: flex;

  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 27px;
  white-space: pre;
  text-decoration: none;

  opacity: 0.5;

  &::after {
    content: "";
    position: absolute;
    z-index: 1;
    left: -2px;
    top: 50%;
    width: calc(100% + 10px);
    height: 100%;

    border-top: 1px solid $primary-text-color;

    transform: rotate(-5deg);
  }
}

.product-information__buy-car-button {
  display: flex;
  padding: 16px 19px;
  margin-bottom: 16px;

  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 19px;
  letter-spacing: 0.12em;
  text-decoration: none;
  text-transform: uppercase;

  color: $interactive-color;
  background-color: $interactive-alternate-color;

  &:hover {
    background-color: $interactive-hover-color;
  }

  &:active {
    opacity: 0.8;
  }
}

.product-information__loan-car-button {
  display: flex;
  padding: 16px 19px;
  margin-bottom: 16px;

  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 19px;
  letter-spacing: 0.12em;
  text-decoration: none;
  text-transform: uppercase;

  color: $interactive-alternate-color;
  background-color: $interactive-color;
  border: 1px solid $interactive-alternate-color;

  &:hover {
    color: $interactive-color;
    background-color: $interactive-alternate-color;
  }

  &:active {
    opacity: 0.8;
  }
}
